:global .hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #3f3f3f;
  color: #dcdcdc;
  -webkit-text-size-adjust: none;
}

:global .hljs-keyword,
:global .hljs-tag,
:global .css .hljs-class,
:global .css .hljs-id,
:global .lisp .hljs-title,
:global .nginx .hljs-title,
:global .hljs-request,
:global .hljs-status,
:global .clojure .hljs-attribute {
  color: #e3ceab;
}

:global .django .hljs-template_tag,
:global .django .hljs-variable,
:global .django .hljs-filter .hljs-argument {
  color: #dcdcdc;
}

:global .hljs-number,
:global .hljs-date {
  color: #8cd0d3;
}

:global .dos .hljs-envvar,
:global .dos .hljs-stream,
:global .hljs-variable,
:global .apache .hljs-sqbracket,
:global .hljs-name {
  color: #efdcbc;
}

:global .dos .hljs-flow,
:global .diff .hljs-change,
:global .python .exception,
:global .python .hljs-built_in,
:global .hljs-literal,
:global .tex .hljs-special {
  color: #efefaf;
}

:global .diff .hljs-chunk,
:global .hljs-subst {
  color: #8f8f8f;
}

:global .dos .hljs-keyword,
:global .hljs-decorator,
:global .hljs-title,
:global .hljs-type,
:global .diff .hljs-header,
:global .ruby .hljs-class .hljs-parent,
:global .apache .hljs-tag,
:global .nginx .hljs-built_in,
:global .tex .hljs-command,
:global .hljs-prompt {
  color: #efef8f;
}

:global .dos .hljs-winutils,
:global .ruby .hljs-symbol,
:global .ruby .hljs-symbol .hljs-string,
:global .ruby .hljs-string {
  color: #dca3a3;
}

:global .diff .hljs-deletion,
:global .hljs-string,
:global .hljs-tag .hljs-value,
:global .hljs-preprocessor,
:global .hljs-pragma,
:global .hljs-built_in,
:global .smalltalk .hljs-class,
:global .smalltalk .hljs-localvars,
:global .smalltalk .hljs-array,
:global .css .hljs-rule .hljs-value,
:global .hljs-attr_selector,
:global .hljs-pseudo,
:global .apache .hljs-cbracket,
:global .tex .hljs-formula,
:global .coffeescript .hljs-attribute {
  color: #cc9393;
}

:global .hljs-shebang,
:global .diff .hljs-addition,
:global .hljs-comment,
:global .hljs-annotation,
:global .hljs-pi,
:global .hljs-doctype {
  color: #7f9f7f;
}

:global .coffeescript .javascript,
:global .javascript .xml,
:global .tex .hljs-formula,
:global .xml .javascript,
:global .xml .vbscript,
:global .xml .css,
:global .xml .hljs-cdata {
